
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import info from "./warningcont.vue";
export default {
components: {
Scroller,
Spinner,
info
},
data() {
return {
listData: [],
warningArr: [],
pullupEnabled: true,
heightObj: {
height: "calc(100% - .9rem)"
},
title: {
name: "safeguarding",
flag: false,
backUrl: "/focus?jump=false",
hiddenGoBack:true
},
status: {
pullupStatus: "default",
pulldownStatus: "default"
},
pullupConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: ""
},
defaultImg:
"this.src='" + require("../../assets/images/1108101904.png") + "'",
warnid: "",
isAndroid: false,
currpage: 1,
showDetail: false,
infoId: "",
isMove: true
};
},
computed: {
classOption() {
return {
step: 0.5,
limitMoveNum: 1, //这个是修改moveSwitch()之前的使用方法，这里的数值指的是数据条数
openTouch: false

};
}
},
created() {
this.refresh();
this.isAndroid = this.$isAndroid();
/* if(!this.isAndroid){
this.title.backUrl='/index';
} */
},
mounted() {
window.setI18nValue = this.$setI18nValue; 
},
methods: {
seamlessClick() {
if (this.isMove) {
this.$refs.seamless.enter();
} else {
this.$refs.seamless.leave();
}
this.isMove = !this.isMove;
},
changeImg(url) {
let imgurl = "";
/* try{
imgurl= require(url);
}catch(e){

imgurl = require('../../assets/images/1108101904.png');
}
return imgurl; */
},
SubCon(_index) {
console.log("===>" + _index);
var obj = this.warningArr[_index];
var infoId = obj.warnid;
this.infoId = infoId;
console.log("openinfo===>" + infoId);
this.showDetail = true;

/* this.$router.push({path:"/warning/cont",query: {
infoId:infoId
}}); */
},
closeInfo() {
this.showDetail = false;
},
errorChange(_this) {
console.log(_this);
},
playM(_index) {
console.log("===>" + _index);
var obj = this.warningArr[_index];
console.log(obj);
if (obj.flag) {
this.warningArr[_index].flag = false;
this.showBottomToast(this.$t("stopplay"));
if (this.isAndroid) {

window.android.stopSpeakVoice();
} else {
console.log("ios init!");
window.location.href = "stopSpeakText";

}
} else {
this.warningArr[_index].flag = true;
var infoId = obj.warnid;
console.log(obj);
this.showBottomToast(this.$t("getcontent"));
var content = "";
var playUrl = "";
content = obj.warncontent;
playUrl = obj.voiceurl;
/* if(this.$i18n.locale=='zh-CN'){
content=obj.warncontent;
playUrl=obj.voiceurl;
} */
/* else if(this.$i18n.locale=='pt-PT'){
content=obj.pt.warncontent;
playUrl=obj.pt.voiceurl;
if(content==undefined||content==''){
content=obj.warncontent;
}
if(playUrl==undefined||playUrl==''){
playUrl=obj.voiceurl;
}
}else{
content=obj.en.warncontent;
playUrl=obj.en.voiceurl;
if(content==undefined||content==''){
content=obj.warncontent;
}
if(playUrl==undefined||playUrl==''){
playUrl=obj.voiceurl;
}
} */
if (this.isAndroid) {


console.log("param==>" + playUrl);
if (playUrl) {
window.android.speakVoiceUrl(playUrl);
}
} else {
var hcontent = encodeURI(content);
window.location.href =
"speakText:" + this.$i18n.locale + "@@@" + hcontent;
console.log("ios init!");
}
}

return false;
},
loadMore() {
var _this = this;
setTimeout(() => {

_this.currpage++;
_this.refresh("1");
setTimeout(() => {
this.$refs.scroller.donePullup();
}, 10);
}, 10);
},
refreshload() {
this.refresh();
},
refresh(qtype) {

var qurl =
this.$myConfig.ip + ":" + this.$myConfig.port + "phone/getMessages";

var _this = this;
if (qtype == undefined) {
_this.currpage = 1;
}
_this.$vux.loading.show({ text: _this.$t("loading") });
var i18nstr = _this.$i18n.locale;
if (i18nstr != "") {
i18nstr = i18nstr.split("-")[0];
}
let a_data= {
key: this.$myConfig.key,
messageType: "ALL",
language: i18nstr,
pageindex: _this.currpage,
pagesize: "10"
};
let method='POST';
let testUrl=window.g.test_getMessages;
if(testUrl) {
qurl=testUrl;
method='GET';
}
$.ajax({
type: method,
url: qurl,
data: a_data, //All 24小是发布过的||NOW 24小时正在发布的


dataType: "json",
async: true,
success: function(data) {

_this.$vux.loading.hide();
_this.$refs.scroller.donePulldown();

if (data.message == "") {
_this.showBottomToast(_this.$t("nohasdata"));
_this.$vux.loading.hide();
return false;
}
var warninglist = data.message;
if (warninglist.length > 0) {
if(typeof(warninglist)==='string') {
warninglist=JSON.parse(data.message);
}else {
for (var i = 0; i < warninglist.length; i++) {
warninglist[i] = JSON.parse(warninglist[i]);
}
}
if (qtype == undefined) {
_this.warningArr = warninglist;
} else {
var data = warninglist;
for (var i = 0; i < data.length; i++) {
_this.warningArr.push(data[i]);
}
}
} else {
if (qtype == undefined) {
_this.warningArr = [];
}
_this.showBottomToast(_this.$t("nohasdata"));
}
_this.$nextTick(() => {
setTimeout(() => {

}, 10);
});
},
error: function(data) {
console.log("請求失敗");
_this.$vux.loading.hide();

}
});

},
getMarqueeData() {
var qurl =
this.$myConfig.ip +
":" +
this.$myConfig.port +
"phone/getInformationWheel?";

var _this = this;
_this.$vux.loading.show({ text: _this.$t("loading") });
var i18nstr = _this.$i18n.locale;
if (i18nstr != "") {
i18nstr = i18nstr.split("-")[0];
}
$.ajax({
type: "GET",
url: qurl,
data: { key: this.$myConfig.key, language: i18nstr },


dataType: "json",
async: true,
success: function(data) {
_this.$vux.loading.hide();
if (data.message == "") {
_this.showBottomToast(_this.$t("nohasdata"));
return false;
}
var marqueelist = JSON.parse(data.message);
if (marqueelist.length > 0) {
_this.listData = [];
_this.listData = marqueelist;
_this.heightObj = {
height: "calc(100% - .9rem - 100px)"
};
_this.$refs.seamless.leave();
_this.isMove = true;
} else {

_this.listData = [];
_this.heightObj = {
height: "calc(100% - .9rem)"
};


}
},
error: function(data) {
console.log("請求失敗");
_this.$vux.loading.hide();
}
});
},
changePullupStatus(enabled) {
if (enabled) {
this.$refs.scroller.enablePullup();
this.pullupEnabled = true;
} else {
this.$refs.scroller.disablePullup();
this.pullupEnabled = false;
}
},
showBottomToast(val) {

this.$showBottomToast(val, "bottom");
},
jumpPhoneDial() {
try {
this.$jumpQuery();
} catch (e) {
console.log(e);
this.$router.push("/query/query");
}
}
}
};



